<template>
  <div class="animated fadeIn">
    <b-card header-tag="header">
      <div slot="header">
        <b-row>
          <b-col>
            <i class="icon-drop"></i> This is Nông Trại
          </b-col>
          <b-col class="text-right">
            <b-dropdown text="View Source" class="m-0 p-0">
              <b-dropdown-item class="p-0" v-for="(x,index) in [
                `/src/components/ComTest1/NongTrai.vue`,
                `/src/store/modules/NongTrai.js`,
                '/src/store/MutationType2.js',
              ]" :key="index">
                  <com-vs2 :file-path-link=" x " ></com-vs2>
              </b-dropdown-item>
            </b-dropdown>
          </b-col>
        </b-row>





      </div>
      <b-card-body>
        <div>{{msg}}</div>
        <button @click="on_create()">create</button>
        <button @click="on_add()">add</button>
        <ol>
          <li v-for="x in ds" :key="x.id">
            <template v-if="sel.id!=x.id">
              {{viewRow(x)}}
              <button @click="on_edit(x)">edit</button>
              <button @click="on_del(x)">del</button>
            </template>
            <template v-else class="my-frm">
              <!-- 
                { "id": 2, "ten": "Nông Trường Sông Hậu", "dia_chi": "Tiền Giang", "dien_tich": 0, 
              "chung_nhan": "Agriculture of America", "ngay_dk": "06/07/2019" } 
              -->
              <b-row class="mb-1">
                <b-col cols="4">id</b-col>
                <b-col cols="8"><input type="text" v-model="sel.id" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">ten</b-col>
                <b-col cols="8"><input type="text" v-model="sel.ten" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">dia_chi</b-col>
                <b-col cols="8"><input type="text" v-model="sel.dia_chi" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">dien_tich</b-col>
                <b-col cols="8"><input type="text" v-model="sel.dien_tich" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">chung_nhan</b-col>
                <b-col cols="8"><input type="text" v-model="sel.chung_nhan" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">ngay_dk</b-col>
                <b-col cols="8"><input type="text" v-model="sel.ngay_dk" class="my-crt" /></b-col>
              </b-row>              

              <button @click="on_update()">update</button>
              <button @click="on_cancel()">cancel</button>
            </template>
          </li>
        </ol>


        <b-modal ref="myModalRef" hide-footer :title=" (sel.id==0) ? 'Thêm Nông Trai' : 'Sửa Nông Trại' ">
          <div class="d-block">
            <div class="frm text-right">
              <h4>This is form</h4>
              <b-row class="mb-1">
                <b-col cols="4">id</b-col>
                <b-col cols="8"><input type="text" v-model="sel.id" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">ten</b-col>
                <b-col cols="8"><input type="text" v-model="sel.ten" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">dia_chi</b-col>
                <b-col cols="8"><input type="text" v-model="sel.dia_chi" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">dien_tich</b-col>
                <b-col cols="8"><input type="text" v-model="sel.dien_tich" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">chung_nhan</b-col>
                <b-col cols="8"><input type="text" v-model="sel.chung_nhan" class="my-crt" /></b-col>
              </b-row>              
              <b-row class="mb-1">
                <b-col cols="4">ngay_dk</b-col>
                <b-col cols="8"><input type="text" v-model="sel.ngay_dk" class="my-crt" /></b-col>
              </b-row>              
              <button v-if="sel.id!=0" @click="on_update();">update</button>
              <button v-if="sel.id==0" @click="on_do_add();">add</button>
              <button @click="on_cancel();">cancel</button>
            </div>
          </div>
        </b-modal>




      
      
      </b-card-body>
    </b-card>


  </div>
</template>

<script>
//import ColorTheme from './ColorTheme'
import Repository2 from '@/store/Repository2';
import types from '@/store/MutationType2';
import moment, { months } from 'moment';

export default {
  components: { 
    //ColorTheme 
  },
  computed:{
    ds(){
      let ds=this.$store.getters[types.NONG_TRAI_GET_DS];
      return ds; 
          // let ds=this.$store.getters[types.NONG_TRAI_GET_DS];
          // this.ds=ds;//res.data.msg;

    }
  },
  data(){
    return{
      //ds:[],
      sel:{},
      msg:""
    }
  },
  created(){
    this.on_lds();
    //alert('created');
    // let ds=this.$store.getters[types.NONG_TRAI_GET_DS];
    // alert(JSON.stringify(ds));
  },
  methods:{
    viewRow(x){
      let kq="";
      let kq2={};
      Object.keys(x).forEach((key,index)=>{
        if(key=="ngay_dk"){
          let ngay_dk=moment(x[key]).format('DD/MM/YYYY');
          kq2[key]=ngay_dk;
        }else{
          kq2[key]=x[key];
        }
      })
      return kq2;
    },
    on_create(){
      Repository2.post('/api_public/farm_list/create',{})
      .then(res=>{
        if(res.data.kq=='ok'){
          this.on_lds();
        }else{
          alert(JSON.stringify(res.data.msg))
        }
      })
    },
    on_del(x){
      var ask = confirm("Are you sure you want to delete?");
      if(!ask){return;}
      this.$store.dispatch(types.NONG_TRAI_DEL, x.id )
      .then(res=>{
        if(res.data.kq=='ok'){
        }else{
          alert(JSON.stringify(res.data.msg))
        }
      })
    },
    on_cancel(){
      this.sel={};
      this.$refs.myModalRef.hide();
    },
    on_lds(){
      this.msg="Loading ... ";
      this.$store.dispatch(types.NONG_TRAI_LDS)
      .then(res=>{
        this.msg="Load done !";
      })
      .catch(err=>{
        alert(JSON.stringify(err))
      })
    },
    on_add(){
      let ngay_dk=moment().format('DD/MM/YYYY');
      this.sel={
        id:0,
        ten:'Ten nong trrai',
        dia_chi:'dia chi',
        dien_tich:0,
        chung_nhan:'Chung Nhan',
        ngay_dk:ngay_dk,
      }
      this.$refs.myModalRef.show();
    },
    on_do_add(){
      let r=Object.assign({},this.sel);
      let ngay_dk=moment(r.ngay_dk,'DD/MM/YYYY').toDate();
      r.ngay_dk=ngay_dk;
      let r2={
        ten:r.ten,
        dia_chi:r.dia_chi,
        dien_tich:r.dien_tich,
        chung_nhan:r.chung_nhan,
        ngay_dk:ngay_dk,
      }
      this.$store.dispatch(types.NONG_TRAI_ADD,r2).then(res=>{
        if(res.data.kq=='ok'){
          this.on_cancel();
        }else{
          alert(JSON.stringify(res.data.msg))
        }
      })

    },
    on_edit(x){
      this.sel=Object.assign({},x);
      let ngay_dk=moment(this.sel.ngay_dk).format('DD/MM/YYYY');
      this.sel.ngay_dk=ngay_dk;
    },
    on_update(){
      let id=this.sel.id;
      let ngay_dk=moment(this.sel.ngay_dk,'DD/MM/YYYY').toDate();
      let r={
        ten:this.sel.ten,
        dia_chi:this.sel.dia_chi,
        dien_tich:this.sel.dien_tich,
        chung_nhan:this.sel.chung_nhan,
        ngay_dk:ngay_dk,
      }
      this.$store.dispatch(types.NONG_TRAI_UPDATE,{id:id,r:r})
      .then(res=>{
        this.on_cancel();
      })
    },
    show_modal: function() {
      this.$refs.myModalRef.show();
    },
    on_close: function() {
      this.$refs.myModalRef.hide();
    }

  }
}
</script>
<style scoped>
  .my-crt{
    width:100%;
  }
</style>
